$frame-width: 1.5em;

.dialog-title {
  font-size: 1rem;
  font-weight: bold;
  width: 100%;
  height: 52px;
  padding: 20px;
  border-bottom: thin solid #E0E0E0;
  box-sizing: border-box;
}

.share-dialog {
  .dialog-content {
    text-align: center;
    padding: 20px !important;
    #preview-container, .generated-image, .share-controls {
      display: inline-block;
      box-sizing: border-box;
      vertical-align: middle;
    }
    .share-controls {
      margin: 0 10px;
      .image-share-btns {
        padding: 20px;
        .image-share-btn {
          font-weight: 500;
          opacity: 0.85;
        }

        .share-btn:hover, .share-btn:active {
          opacity: 1;
        }
      }
    }

    #preview-container {
      width: 268px;
      height: 228px;
    }

    .generated-image {
        width: 268px;
        height: 268px;

        img {
          max-width: 100%;
          max-height: 100%;
        }
    }
  }

  .dialog-actions {
    border-top: thin solid #E0E0E0;
    padding-right: 12px;
    & > * {
      margin: 0 4px !important;
    }
  }

  #preview {
    text-align: center;
    margin: 0 auto;
    
    .gen-target {
      transform-origin: top left;
      transform: scale(0.25);
      height: 400%;
      width: 400%;
      font-size: 4em;
      display: flex;
      flex-direction: column;
      position: relative;
    }
    .title, .subtitle {
      font-family: Helvetica, Arial, sans-serif;
      margin: 0 0.67em;
    }
    .title {
      font-size: 1em;
      font-weight: 500;
      margin-top: 0.4em;
    }
    .subtitle {
      font-size: 0.9em;
      margin-bottom: 0.35em;
      padding-left: 0.05em;
    }
    .plot-frame {
      padding: 0 $frame-width $frame-width $frame-width;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      height: 100%;

      .bg {
        background-color: #FAFBFC !important;
        -moz-box-shadow:    inset 0 0 4px #334d4d;
        -webkit-box-shadow: inset 0 0 4px #334d4d;
        box-shadow:         inset 0 0 4px #334d4d;
      }
    }
    .x-axis, .y-axis {
      position: absolute;
      font-size: 1em;
    }
    .x-axis {
      bottom: 0.1em;
      left: 3em;
    }
    .y-axis {
      -webkit-transform: rotate(270deg);
      transform: rotate(270deg);
      text-align: left;
      width: 100%;
      bottom: 23.5em;
      left: calc(-50% + 0.85em);
    }
  }

  .row {
    justify-content: space-between;
  }

  .top-padded {
    margin-top: 0.5em;
  }

  .share-controls {
    .size-inputs {
      margin-top: 1em;

      .custom-size-inputs {
        width: 100%;
      }
      .custom-size-input {
        width: 4em;
      }
    }
  }

  .caption {
    display: block;
    text-align: center;
    transform: scale(0.75);
    opacity: 0.6;
    margin: 0 0 5px 0;
  }

  .social-btns {
    display: flex;
    justify-content: space-between;
    margin-bottom: 7px;
    svg {
      width: 44px;
      height: 44px;
    }
  }

  .copy-btn {
    margin-top: -7px;
    margin-right: -15px;
  }

  .legend {
    margin: $frame-width * 1.75;
  }
}

.preview-light-mode #preview {
  .legend {
    color: #000;
    background-color: #FFF;
  }
  .title, .subtitle, .plot-frame {
    color: #000;
    background-color: #E6E6E6;
  }
  .x-axis, .y-axis {
    color: #000;
  }
}

.preview-dark-mode #preview {
  color: #FFF;
  .plot {
    color: #FFF;
    background-color: #121212 !important;
  }

  .legend {
    background-color: #121212AA;
  }

  .title, .subtitle, .plot-frame {
    background-color:  #3D3D3D !important;
  }
}
