.timeline {
  display: flex;
  padding: 5px 1.5rem 0 0;
  box-sizing: border-box;
  align-items: start;
  .play {
    margin-right: 0.25rem;
    width: 41px;
    height: 41px;
    svg {
      width: 36px;
      height: 36px;
    }
  }
}

@media (pointer: coarse) {
  .custom-slider {
    padding: 16px 0 !important;
  }
}
