.custom-select {
  width: 100%;
  margin-top: 16px;
  display: inline-flex;
  position: relative;
}

.custom-select {
  &:before {
    border-bottom: 1px solid rgba(0, 0, 0, 0.42);
    left: 0;
    bottom: 0;
    content: "\00a0";
    position: absolute;
    right: 0;
    -webkit-transition: border-bottom-color 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    transition: border-bottom-color 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    pointer-events: none;
  }
}

.compare-by-fieldset {
  width: 100%;
  display: flex;

  .fields {
    flex-grow: 1;
  }
  .switch {
    flex-direction: column;
    display: flex;
    justify-content: center;
    padding-top: 22px;
    margin-left: 6px;
    margin-right: -6px;
  }
}

.custom-select .adornment {
  vertical-align: bottom;
  float: right;
}

.custom-select, .custom-select * {
  cursor: pointer !important;
}

.custom-select > .MuiSelect-select {
  white-space: nowrap;
  overflow: hidden;
  flex-grow: 1;
  line-height: 1.4375em;
  display: inline-flex;
  position: relative;
  padding: 5px 4px 4px 4px;
}

.Mui-disabled, .Mui-disabled * {
  color: rgba(0, 0, 0, 0.12) !important;
  -webkit-text-fill-color: rgba(0, 0, 0, 0.12) !important;
  cursor: not-allowed !important;

  &::before {
    border-bottom: thin rgba(0, 0, 0, 0.12) dotted !important;
  }
}

li.menu-group-header {
  font-style: italic;
  color: rgba(0, 0, 0, 0.6) !important;
  -webkit-text-fill-color: rgba(0, 0, 0, 0.6) !important;
  opacity: 1 !important;
}

.select-drawer-bar {
  background-color: #FAFBFC;
  border-bottom: thin solid #CECFD1;
  height: 2.25em;
  position: fixed;
  width: 100%;
  z-index: 999999;

  .title {
    line-height: 2.25em;
    float: left;
    opacity: 0.6;
    font-style: italic;
    padding-left: 15px;
  }
  .actions {
    float: right;
    padding-right: 5px;
  }
}

.select-drawer-content {
  padding-top: 45px;
  margin-bottom: 30px;
  overflow-y: scroll;

  .MuiListItemText-secondary {
    white-space: normal;
  }
}

.dark-mode {
  .custom-select {
    &:before {
      border-color: rgba(255, 255, 255, 0.7);
    }
  }
  .Mui-disabled, .Mui-disabled * {
    color: rgba(255, 255, 255, 0.12) !important;
    -webkit-text-fill-color: rgba(255, 255, 255, 0.12) !important;
    cursor: not-allowed !important;
  
    &::before {
      border-bottom: thin rgba(255, 255, 255, 0.12) dotted !important;
    }
  }
  .select-drawer-bar {
    background-color: #3D3D3D;
    border-color: #666;
  }
  .select-drawer-content {
    background-color: #121212;
  }
  li.menu-group-header {
    color: rgba(255, 255, 255, 0.6) !important;
    -webkit-text-fill-color: rgba(255, 255, 255, 0.6) !important;
    opacity: 1 !important;
  }
}
