#site-title {
  font-size: 1.25rem;
  line-height: 2.25;
  letter-spacing: 0.0075em;
  font-weight: 400;
  white-space: nowrap;
}

.side-menu {
  width: 270px;
  font-size: 1.2rem;
  color: #393939;

  a {
    display: flex;
    text-decoration: none;
    color: inherit;
  }

  .menu-item-icon {
    min-width: 46px;
    padding-left: 4px;
  }

  .nested-menu {
    .MuiList-padding {
      padding: 0.25rem 0;
    }
    .MuiListItem-root {
      padding-top: 0;
    }
    span {
      padding-left: 47px;
      font-size: 0.85rem;
    }
  }
}
