$primary-color: #297ACC;
$controls-width: 220px;

.plot-area {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  background-color: #EAECEE !important;
}

#root {
  display: flex;
  flex-direction: column;
}

a:hover {
  text-decoration: underline;
}

a, a.btn:hover {
  text-decoration: none;
}

#home, #about, #dashboard {
  padding: 17px 16px 8px 16px;
  box-sizing: border-box;
}

#home, #about {
  .section {
    padding: 20px;
  }
  main .section {
    padding-top: 0;
    & > * {
      max-width: 800px;
    }
  }
}

#home #presets-table {
  width: 100%;
  border-collapse: collapse;
  th {
    font-size: 1em;
    font-weight: 300;
    opacity: 0.6;
  }
  td {
    border: thin solid #80808020;
    padding: 10px 16px;
  }
}

#dashboard {
  flex: 1 1;
  width: 100%;
  display: block;
  float: left;
}

.plot {
  position: relative;
}

.loading-plot {
  position: absolute;
  display: flex;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.5);
  opacity: 0.875;

  & > * {
    margin: auto;
  }
}

#dashboard aside {
  text-align: center;
  .controls {
    display: inline-block;
    width: $controls-width;
    padding-left: 16px;
    text-align: left;
    .plot-type {
      text-align: center;
      margin-top: 1px;
    }
  }
}

.form-control {
  margin-top: 20px !important;
  display: block !important;
}

.multiple-select-indeterminate {
  color: $primary-color;
}

.multiple-select-select-all {
  font-weight: 500;
}

.multiple-select-selected-all {
  background-color: rgba(0, 0, 0, 0.08);
  &:hover {
    background-color: rgba(0, 0, 0, 0.08);
  }
}

.grow {
  flex-grow: 1;
}

.section {
  padding: 10px;
  background-color: #FAFBFC !important;
  box-sizing: border-box;
}

.axis line {
  stroke: #000;
}

.axis text {
  fill: #000;
}

.tooltip {
  background-color: #FAFBFC !important;
  opacity: 0.95;
  z-index: 9999;  /* Appear over the app bar */
  padding: 0.6em !important;
}

.tooltip-content {
  color: #000;
  text-align: left;
  .tooltip-title {
    h3 {
      float: left;
      font-weight: 500;
      font-size: 1em;
      margin: 0;
      padding: 0.05em 0.5em 0 0;
      line-height: 1.55em;
    }

    .year {
      float: right;
      padding: 0 0.5em;
      margin-bottom: 0.2em;
      line-height: 1.55em;
      font-weight: 400;
      font-size: 0.95em;
      border-radius: 0.25em;
      color: white;
    }
  }

  .tooltip-body {
    clear: both;
    opacity: 0.75;
    line-height: 1.6em;
  }
}

.plot-area {
  width: 100%;
  height: calc(100% - 40px);
  .section {
    height: 100%;
  }
}

#inner-plot-container {
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;

  .x-axis {
    text-align: center;
    position: absolute;
    bottom: -16px;
    width: 100%;
  }
  .y-axis {
    transform: rotate(270deg);
    transform-origin: left;
    text-align: center;
    position: relative;
    left: -7px;
    bottom: 3%;
  }
}

.plot-label {
  fill: #202122;
  opacity: 0.9;
  font-size: 0.9rem;
}

.MuiDrawer-root .MuiPaper-root {
  background-color: #EAECEE !important;
}

a.screenshot {
  img {
    border: thin solid #80808040;
    box-sizing: border-box;
    margin: 0 0 15px 0;
    display: block;
  }
}

img.logo {
  width: 96px;
  height: 96px;
  float: right;
  margin-top: 20px;
  margin-left: 10px;
  opacity: 0.85;
}

@media screen and (max-width: 639px) { /* Mobile */
  .plot-area {
    font-size: 15px !important;
  }
  #dashboard main {
    height: 100vw;
    max-height: 420px;
  }
  aside {
      padding-top: 10px;
      padding-bottom: 10px;
    & > * {
      width: 100%;
    }
  }
  #home, #about {
    aside {
      margin-top: 8px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
  a.screenshot {
    width: calc(50% - 8px);
  }
}

@media screen and (max-width: 359px) { /* Small mobile */
  .plot-area {
    font-size: 13px !important;
  }
}

@media screen and (min-width: 640px) { /* Desktop */
  #root {
    height: 100%;
  }

  .top-bar {
    padding-right: $controls-width;
  }

  main {
    width: 100%;
    display: flex;
    flex-direction: column;
    float: left;
    margin-right: 0 - $controls-width;
    padding-right: $controls-width;
    box-sizing: border-box;
  }

  aside {
    padding-top: 1px;
  }

  #dashboard {
    main {
      height: 100%;
    }
    aside {
      height: 100%;
    }
  }

  #home, #about {
    aside {
      float: right;

      &, & > * {
        width: $controls-width - 16px;
      }
      img {
        aspect-ratio: 9/7;
      }
    }
  }
}

body.light-mode {
  color: #000;

  .map-poly {
    stroke: #FAFBFC;
  }
}

body.dark-mode {
  color: white;
  background-color: #3D3D3D !important;

  a:not(.MuiButton-root) {
    color: #7BF;
  }

  a:visited:not(.MuiButton-root) {
    color: #C9F;
  }

  a:active:not(.MuiButton-root) {
    color: #F99;
  }

  .side-menu a {
    color: white !important;
  }

  .section, .gen-target {
    background-color: #121212 !important;
  }

  .MuiDrawer-root .MuiPaper-root {
    background-color: #3D3D3D !important;
  }

  .map-poly {
    stroke: #222222;
  }

  .side-menu {
    color: white;
  }

  .axis line {
    stroke: #FFF;
  }

  .axis text, .plot-label {
    fill: #FFF;
  }

  .tooltip {
    background-color: #121212 !important;
    border: thin solid #717273;
    box-shadow: none;

    .year {
      color: black;
    }
  }

  .tooltip-content {
    color: #FFF;
  }

  .MuiButton-text {
    color: #7BF;
  }
}
