.legend {
  position: absolute;
  font-size: 0.75em;
}

.legend.nw {
  top: -5px;
  left: -5px;
}

.legend.ne {
  top: -5px;
  right: -5px;
}

.legend.sw {
  bottom: -5px;
  left: -5px;
}

.legend.se {
  bottom: -5px;
  right: -5px;
}

.layer, .strip {
  position: relative;
}

.layer, .strip {
  width: 100%;
}

.ordinal .labels {
  margin-top: -0.25em;
}

.layer {
  border-radius: 2px;
  height: 100%;
}

.ordinal, .nominal {
  width: 8.5em;
}

table.nominal-grid {
  margin-right: 0.25em;
  margin-bottom: 0.125em;
}

th.row-header {
  padding-right: 0.25em;
}

td.cell {
  border: 0.125em solid #FAFBFC;
}

.strip {
  height: 1.5em;
  padding: 0 0.5em;
  text-align: left;
  &:not(.labels) {
    color: black;
  }
  &:not(:last-of-type) {
    margin-bottom: 0.5px;
  }
}

.label, .strip, .cell {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.label {
  position: absolute;
}

.nominal-grid {
  th {
    font-size: 1em;
    font-weight: 400;
  }
  td {
    width: 2em;
    height: 2em;
  }
}

.preview-light-mode #preview {
  .legend {
    background-color: #FAFBFCAA;
  }
  td.cell {
    border-color: #FAFBFC;
  }
}

.dark-mode, .preview-dark-mode #preview {
  .legend {
    background-color: #1E1E1EAA;
  }
  td.cell {
    border-color: #121212;
  }
}
